    -webkit-animation: header 800ms linear;
            animation: header 800ms linear;
    transition: all 300ms ease-out;
}
.heading {
    color: whitesmoke;
    margin: 10px auto 0 auto;
    border-bottom: 1px solid whitesmoke;
